import React, { useState, useEffect } from "react";



const Option = ({text,type}) => {
  return (
    <div className="option">
      <div className="option_content shake">
        {
          {
            'yt':<i class="fab fa-youtube icon"></i>,
            'hacks':<i class="fas fa-download icon"></i>,
            'injector':<i class="fas fa-syringe icon"></i>,
          }[type]

        }
        {/* {
          type == "yt" ?
          <i class="fab fa-youtube icon"></i>
          :
          null
        } */}
        <span className="option_text">{text}</span>
      </div>

      

    </div>
  );
};

export default Option;
